.header{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(29, 5, 58, 0.9);
    transition: 0.5s;
    
}

.nav-menu {
    display: flex;

}

.nav-menu li{
padding-top: 0px;
  padding-right: 16px;
  padding-left: 16 px;
}

/*styling the link tage in navbar.js*/
.nav-menu li a {
    font-size: 1 2rem;
    font-weight: 500;

}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgb(0, 0, 0,0.9);
        position: absolute;
        top: 0%;
        left: -100%;
        z-index: -3;
        transition: 0.4s;

    }

    .nav-menu.active{
        left:0;

    }

    .hamburger{
        display:none;
    }
    .hamburger{
        display:initial;
    }

    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a{
        font-size: 2rem;
    }
}

