label{
    color: aliceblue;
    margin-bottom: 0.5rem;
}

form{
   
    display:flex;
    flex-direction: column;
    padding:4rem 1rem;
    margin:auto;
    max-width: 600px;
}
form input, form textarea{
    margin-bottom:1rem;
    padding:10px 18px;
    font-size: 1.2rem;
    background-color: #f4e1f1;
    border-color: aliceblue;
    border-radius: 10px;
    color: rgb(1, 10, 19);

}